@import '../colors/colors.scss';
@import '../fonts/main.scss';
@import '../borders/main.scss';

.button,
button {
    border-radius: 0;
    height: 50px;
    font-family: $header-font;
    font-size: $font-size-button;
    line-height: 30px;
    color: $color-gov-link;

    & i {
        font-size: $icon-large-button-size;
    }

    &.icon-only {
        height: auto;
        width: auto;
        border: none;

        &>i {
            padding: 0;
        }
    }

    cursor: pointer;

    &:active,
    &:focus {
        border: $focus-border;
        background-color: initial;
    }

    &.primary-button,
    &.secondary-button,
    &.tertiary-button,
    &.quaternary-button {
        padding-left: $large-padding;
        padding-right: $large-padding;

        i {
            padding-right: $standard-padding;
            padding-bottom: 4px;
        }
    }

    &.small-button {
        height: 35px;
        line-height: 10px;

        i {
            font-size: $icon-small-button-size;
        }
    }

    &.medium-button {
        height: 45px;

        i {
            font-size: $icon-medium-button-size;
        }
    }

    &:disabled {
        pointer-events: none;
        opacity: 0.5;
    }


    &.secondary-button,
    &.primary-button:hover,
    &.primary-button:active,
    &.primary-button:focus {
        background-color: $color-main-background!important;
        border: 2px solid $color-gov-link;
        color: $color-gov-link;

        &.delete-button {
            color: $color-delete;
            border: 2px solid $color-delete;
            background-color: $color-main-background;

            i {
                color: $color-delete;
            }
        }

        i {
            color: $color-gov-link;
        }




    }

    &.primary-button,
    &.secondary-button:active,
    &.secondary-button:hover,
    &.secondary-button:focus {
        background-color: $color-primary-button!important;
        color: $color-alternative-text;
        border: 2px solid $color-primary-button;

        &.delete-button {
            background-color: $color-delete;
            border: 2px solid $color-delete;
        }

        i {
            color: $color-alternative-text;
        }


    }

    &.tab-list-item,
    .tab-list-active {
        height: 45px;
        line-height: 25px;
        margin-bottom: $standard-padding;
        color: $color-gov-link;
        background-color: white;
        border: 2px solid $color-gov-link;
    }

    &.tab-list-item+.tab-list-item {
        margin-right: $x-large-padding;
    }

    &.tab-list-active {
        background-color: #dde5ed;
        border: 2px solid #dde5ed;
        color: #212427;
    }

    &.tertiary-button,
    &.tertiary-button:hover,
    &.tertiary-button:active,
    &.tertiary-button:visited,
    &.tertiary-button:focus:active {
        background-color: $color-main-background;
        color: $color-main-text;
        border-color: $color-border;

        &>i {
            color: $color-main-text;
        }
    }

    &.quaternary-button {
        background-color: $color-completed;
        color: white;
    }

    &.fas fa-check-circle {
        border-radius: 50%;
        height: 40px;
        width: 40px;

        // font-size: ;
        icon {
            padding: 0;
            margin: 0;
        }
    }

    &.link {
        color: $color-gov-link;
        height: auto;
    }

    &.back-button {
        color: $color-gov-link;

        i {
            color: $color-gov-link;
        }
    }

    &.search-button{
        padding:0;
        width: 40px;
        min-width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        i{
            padding:0
        }
    }
}

button {
    padding-left: $standard-padding;
    padding-right: $standard-padding;

    &.small-button {
        height: 35px;
        line-height: 10px;
    }

    &:disabled {
        pointer-events: none;
        opacity: 0.5;
    }
}

//override here as imported from great styles
.button--only-icon {
    height: 40px;
    width: 40px;
    border-radius: 50% !important;
    border: 2px solid $color-gov-link;

    &>i {
        padding: 0;
    }
}

.tooltip__icon>button {
    i.fas.fa-book {
        color: $color-gov-link  !important;
    }
}

button.button-example,
button.button-lesson {
    color: $color-main-text;

    i {
        color: $color-main-text;
    }
}
